.app__cart-main {
  display: flex;
  width: 80%;
  margin: auto;
  color: black;
  flex-direction: column;
}

.app__cart-sub{
  display: flex;
  width: 100%;
  flex-direction: row;
}

.app__cart-main ul {
  padding: 0.5rem;
}

.app__cart-main li {
  list-style: none;
  margin: 0.3rem;
  padding: 0.3rem;
}

.app__cart-img {
  display: block;
  height: 100%;
  width: 40px;
}

.app__cart-item {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.app__cart-header {
  display: flex;
  /* border-radius: 5px; */
  padding: 1rem 2rem;
  background-color:  rgb(235, 233, 233);
  justify-content: space-between;
}


.header-left{
  display: flex;
}

.header-left > svg{
  width: 5rem;
  height: 5rem;
}

.app__cart-itemcount{
  display: block;
  align-self: center;
}

.app__cart-itemcount > *{
  margin: 10px;
  font-size: 18px;
}
/* 
.app__cart-header > div{
  display: flex;
  flex-direction: row;
} */

.app__cart-desc {
  display: flex;
  padding-left: 2rem;
  font-size: 18px;
  align-items: center;
}

.app__cart-item-collapse {
  /* border-radius: 5px; */
  background-color: rgb(251, 252, 252);
  border: 1px solid #ccc;
  overflow: hidden;
}

.app__item-form > .form__fields{
  display: flex;
  justify-content: space-around;
}

.form__submit{
  display: flex;
  padding-top: 2%;
  justify-content: center;
}

.form__submit > button{
  display: block;
  padding: 0.5%;
  border: 1px solid grey;
  border-radius: 5px;
  cursor: pointer;
}

.form__submit > button:hover{
  background-color: black;
  color: white;
}

.app__item-field > *{
  display: flex;
  padding: 4px;
  text-align: center;
}

.app__item-field > input, .app__item-field > select{
  display: block;
  border-radius: 8px;
}

.Cart__submit{
  margin-top: 4%;
  text-align: center;
}

.Cart__submit > button{
  padding: 0.8%;
  border: 1px solid black;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  background-color: black;
  color: white;
}

.Cart__submit > button:hover{
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.1);
}

.Cart-details{
  display: flex;
  width: 60%;
}

.Cart-details form{
  width: 100%;
}

.Cart-amount{
  display: flex;
  width: 30%;
}
